import Spaceship from './Spaceship.js'
import Scenery from './Scenery.js'
import Info from './Info.js'

export default class Game {
    constructor(canvas) {
        this.started = false
        this.score = 0
        this.time = 0
        this.width = canvas.width
        this.height = canvas.height
        this.spaceship = new Spaceship(canvas, 15)
        this.scenery = new Scenery(canvas)
        this.info = new Info(this.spaceship, canvas)

    }

    registerKeyListeners() {

        const game = this

        navigator.bluetooth.requestDevice({acceptAllDevices:true}).then(device => {
            console.log(device.name + " " + device.id + " " + device.gatt.connected);
          })
          .catch(error => {
            console.log('Argh! ' + error);
          });

        document.addEventListener( 'keyup',function(event) {
            switch(event.keyCode)
            {
                case 32:
                    game.started = true
                    game.spaceship.reset()
                    game.spaceship.state = 'active'
                    break;
                case 37:
                    // Left Arrow key
                    game.spaceship.rotatingLeft = false
                    break;
                case 39:
                    // Right Arrow key
                    game.spaceship.rotatingRight = false
                    break;
                case 38:
                    // Up Arrow key
                    game.spaceship.engine.on = false
                    break;
            }
        })
        
        document.addEventListener( 'keydown',function(event) {
            switch(event.keyCode)
            {
                case 37:
                    // Left Arrow key
                    game.spaceship.rotatingLeft = true
                    break;
                case 39:
                    // Right Arrow key
                    game.spaceship.rotatingRight = true
                    break;
                case 38:
                    // Up Arrow key
                    game.spaceship.engine.on = true
                    break;
            }
        })
    }

    update() {
        
        this.scenery.collision(this.spaceship)
        if(this.spaceship.state == 'crashed' || this.spaceship.state == 'landend') {
            this.started = false
        } else {
            this.spaceship.update()
        }
    }
    
    draw(context) {
        this.scenery.draw(context)
        this.info.draw(context)
        this.spaceship.draw(context)
        if(!this.started) {
            context.beginPath()
            context.font = '40px Lucida';
            context.fillStyle = 'white';
            context.textAlign = 'center'
            context.fillText( "Press [spacebar] to start.", Game.width / 2, Game.height / 2);
            context.closePath()
        }
    }
}
